"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { 
  Search, ChevronLeft, Clock, Calendar, 
  ShoppingBag, Filter, ArrowUpDown, Loader2,
  Timer, Star, MessageSquare
} from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import logo from "@/lib/images/image copy.png"

interface Product {
  productId: string;
  advisorId: string;
  productName: string;
  description: string;
  category: string;
  durationMin: number;
  price: number;
  dayOfWeek: string;
  startTime: number;
  endTime: number;
  reserved?: boolean;
}

const formatTime = (time: number) => {
  const timeStr = time.toString().padStart(4, '0');
  return `${timeStr.slice(0, 2)}:${timeStr.slice(2)}`;
};

const CATEGORIES = ["전체", "자산관리", "보험", "부동산", "세무", "투자"];

export default function ProductsPage() {
  const router = useRouter()
  const [isLoading, setIsLoading] = useState(true)
  const [products, setProducts] = useState<Product[]>([])
  const [searchTerm, setSearchTerm] = useState("")
  const [selectedCategory, setSelectedCategory] = useState("전체")

  const fetchProducts = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch("http://34.50.7.8:30000/v1/products", {
        headers: { "Authorization": `Bearer ${token}` }
      })
      if (res.ok) {
        const data = await res.json()
        setProducts(data.result || data)
      }
    } catch (error) {
      console.error("상품 로드 실패:", error)
    } finally {
      setIsLoading(false)
    }
  }, [])

  useEffect(() => { fetchProducts() }, [fetchProducts])

  // 필터링 로직
  const filteredProducts = products.filter(prod => {
    const matchesSearch = prod.productName.toLowerCase().includes(searchTerm.toLowerCase()) || 
                         prod.description.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === "전체" || prod.category === selectedCategory;
    return matchesSearch && matchesCategory;
  })

  if (isLoading) return (
    <div className="min-h-screen flex items-center justify-center bg-background">
      <Loader2 className="h-10 w-10 animate-spin text-primary" />
    </div>
  )

  return (
    <div className="min-h-screen bg-slate-50/50">
      {/* 헤더 */}
      <header className="bg-white border-b border-border sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" onClick={() => router.back()}>
              <ChevronLeft className="h-5 w-5" />
            </Button>
            <Link href="/dashboard">
              <Image src={logo} alt="Next Me" width={100} height={28} className="h-7 w-auto" />
            </Link>
          </div>
          <h1 className="text-sm font-black absolute left-1/2 -translate-x-1/2 hidden sm:block">전문가 서비스 탐색</h1>
          <div className="w-10" /> {/* 밸런스용 빈 공간 */}
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {/* 상단 검색 및 필터 구역 */}
        <div className="max-w-4xl mx-auto space-y-6">
          <div className="flex flex-col gap-4">
            <h2 className="text-2xl font-black tracking-tight text-slate-900">어떤 전문가의 도움이<br />필요하신가요?</h2>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input 
                placeholder="상품명 또는 내용으로 검색..." 
                className="pl-10 h-12 bg-white border-slate-200 shadow-sm rounded-xl focus-visible:ring-primary"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
          </div>

          <div className="flex items-center gap-2 overflow-x-auto pb-2 scrollbar-hide">
            {CATEGORIES.map(cat => (
              <Button
                key={cat}
                variant={selectedCategory === cat ? "default" : "outline"}
                size="sm"
                onClick={() => setSelectedCategory(cat)}
                className={`rounded-full px-5 font-bold whitespace-nowrap text-xs h-9 ${
                  selectedCategory === cat ? "bg-primary shadow-md shadow-primary/20" : "bg-white"
                }`}
              >
                {cat}
              </Button>
            ))}
          </div>

          <div className="flex items-center justify-between text-xs font-bold text-slate-500 px-1">
            <p>총 {filteredProducts.length}건의 전문가 상품</p>
            <div className="flex items-center gap-1 cursor-pointer hover:text-primary">
              <ArrowUpDown className="h-3 w-3" /> 추천순
            </div>
          </div>

          {/* 상품 리스트 그리드 */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {filteredProducts.length > 0 ? filteredProducts.map((prod) => (
              <Card key={prod.productId} className="group relative bg-white border-slate-200/60 hover:border-primary/40 transition-all duration-300 overflow-hidden shadow-sm hover:shadow-xl rounded-2xl">
                <div className="p-6">
                  <div className="flex justify-between items-start mb-4">
                    <Badge variant="secondary" className="bg-primary/5 text-primary border-none font-black text-[10px] px-2.5 py-1 uppercase">
                      {prod.category}
                    </Badge>
                    <p className="text-xl font-black text-slate-900">₩{prod.price.toLocaleString()}</p>
                  </div>

                  <div className="space-y-2 mb-6">
                    <h3 className="text-lg font-bold group-hover:text-primary transition-colors line-clamp-1">{prod.productName}</h3>
                    <p className="text-sm text-slate-500 leading-relaxed line-clamp-2 min-h-[40px]">{prod.description}</p>
                  </div>

                  <div className="grid grid-cols-2 gap-2 mb-6">
                    <div className="flex items-center gap-2 text-[11px] font-bold text-slate-600 bg-slate-50 p-2 rounded-lg">
                      <Clock className="h-3.5 w-3.5 text-primary/60" /> {prod.durationMin}분 상담
                    </div>
                    <div className="flex items-center gap-2 text-[11px] font-bold text-slate-600 bg-slate-50 p-2 rounded-lg">
                      <Calendar className="h-3.5 w-3.5 text-primary/60" /> {prod.dayOfWeek}
                    </div>
                    <div className="flex items-center gap-2 text-[11px] font-bold text-slate-600 bg-slate-50 p-2 rounded-lg col-span-2">
                      <Timer className="h-3.5 w-3.5 text-primary/60" /> {formatTime(prod.startTime)} ~ {formatTime(prod.endTime)} 운영
                    </div>
                  </div>

                  <Button 
                    className="w-full h-12 rounded-xl font-black text-sm bg-slate-900 hover:bg-primary transition-all duration-300 shadow-lg shadow-slate-200"
                    onClick={() => router.push(`/dashboard/products/${prod.productId}`)}
                  >
                    전문가 상담 예약하기
                  </Button>
                </div>
                
                {prod.reserved && (
                  <div className="absolute inset-0 bg-white/60 backdrop-blur-[1px] flex items-center justify-center z-10">
                    <Badge variant="outline" className="bg-white text-slate-400 border-slate-200 font-black px-4 py-2 text-sm shadow-sm">
                      현재 예약 마감
                    </Badge>
                  </div>
                )}
              </Card>
            )) : (
              <div className="col-span-full py-20 text-center bg-white rounded-3xl border border-dashed border-slate-300">
                <ShoppingBag className="h-12 w-12 text-slate-200 mx-auto mb-4" />
                <p className="text-slate-400 font-bold">해당하는 전문가 상품이 없습니다.</p>
                <Button variant="link" onClick={() => {setSearchTerm(""); setSelectedCategory("전체")}} className="mt-2 text-primary font-bold">
                  필터 초기화하기
                </Button>
              </div>
            )}
          </div>
        </div>
      </main>
    </div>
  )
}